@import url('https://fonts.googleapis.com/css?family=Source+Sans+Pro:300,300i,400,400i,600,600i,700&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Source Sans Pro', sans-serif;
}

body {
  background: url(./assets/todo.svg);
  background-repeat: no-repeat;
  background-size: cover;
  font-size: 16px;
}

.todo-app {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 70%;
  min-height: 100vh;
  background: #161a2b;
  text-align: center;
  margin: 0 auto;
  padding-bottom: 20px;
  padding-top: 20px;
  float: right;
}

h1 {
  margin: 32px 0;
  color: #fff;
  font-size: 24px;
}

.complete {
  text-decoration: line-through;
  opacity: 0.4;
}

.todo-form {
  margin-bottom: 30px;
}

.todo-input {
  padding: 14px 32px 14px 16px;
  border-radius: 4px 0 0 4px;
  border: 2px solid teal;
  outline: none;
  width: 100%;
  background: transparent;
  color: #fff;
  border-radius: 15px 0 15px 0;
  font-size: 16px;
}

.todo-input::placeholder {
  color: #e2e2e2;
}

.todo-button {
  padding: 16px;
  border: none;
  border-radius: 15px 0 15px 0;
  cursor: pointer;
  outline: none;
  background: teal;
  color: #fff;
  text-transform: uppercase;
  display: block;
  width: 100%;
  margin-top: 20px;
}

.todo-input.edit {
  border: 2px solid orange;
}

.todo-button.edit {
  background: orange;
  padding: 16px 22px;
}

.todo-container {
  display: flex;
  flex-direction: row;
  position: relative;
}

.todo-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 4px auto;
  color: #fff;
  background: teal;

  padding: 16px;
  border-radius: 5px;
  width: 60%;
}

.todo-row:nth-child(2n + 1) {
  background: grey;
}

.icons {
  display: flex;
  align-items: center;
  font-size: 24px;
  cursor: pointer;
}

.delete-icon {
  margin-right: 5px;
  color: #fff;
}

.edit-icon {
  color: #fff;
}

@media only screen and (max-width: 1024px) {
  .todo-app {
    width: 100%;
  }
}
